import React, { useEffect, useState } from "react";
import PageNotFound from "./PageNotFound";
import { projects } from "../content/projects";
import SimpleImageSlider from "react-simple-image-slider";

export default function ProjectScreen({ title }) {
  const [windowSize, setWindowSize] = useState(getWindowSize());

  useEffect(() => {
    function handleWindowResize() {
      setWindowSize(getWindowSize());
    }

    window.addEventListener("resize", handleWindowResize);

    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);

  //Check if screen exists
  if (!projects.hasOwnProperty(title)) {
    return <PageNotFound />;
  }

	const project = projects[title];

  const sliderSize = {
    width: 853,
    height: 480,
  };

  switch (true) {
    case windowSize.innerWidth >= 1536:
      sliderSize.width = 1100;
      sliderSize.height = 618;
      break;
    case windowSize.innerWidth >= 1280:
      sliderSize.width = 956;
      sliderSize.height = 542;
      break;
    case windowSize.innerWidth >= 1024:
      sliderSize.width = 956;
      sliderSize.height = 542;
      break;
    case windowSize.innerWidth >= 768:
      sliderSize.width = 768;
      sliderSize.height = 432;
      break;
    case windowSize.innerWidth >= 640:
      sliderSize.width = "100%";
      sliderSize.height = 360;
      break;
    case windowSize.innerWidth >= 258:
      sliderSize.width = "100%";
      sliderSize.height = 360;
      break;
    case windowSize.innerWidth >= 375:
      sliderSize.width = "100%";
      sliderSize.height = 210;
      break;
    default:
      sliderSize.width = 853;
      sliderSize.height = 480;
      break;
  }

  return (
    <main>
			<div className="flex justify-center mb-6">
        <div className="flex flex-col items-center">
					<h1 className="font-bold text-4xl font-inter">{project.title}</h1>
				</div>
      </div>
      <div className="flex justify-center">
        <SimpleImageSlider
          bgColor="rgba(0,0,0,0)"
          width={sliderSize.width}
          height={sliderSize.height}
          images={project.images ?? [{ url: "../images/Forbidden_Symbol.svg" }]}
          style={{ borderRadius: "0.75rem" }}
          showBullets={true}
          showNavs={true}
        />
      </div>
			<div className="flex justify-center mt-6">
        <div className="flex flex-col items-center" style={{maxWidth: sliderSize.width !== "100%" ? sliderSize.width + "px" : sliderSize.width}}>
					<p className="font-quicksand text-center text-xl">{project.description}</p>
				</div>
      </div>
    </main>
  );
}

function getWindowSize() {
  const { innerWidth, innerHeight } = window;
  return { innerWidth, innerHeight };
}
